import { ContactsList, Contact, Name, Phone } from './Contacts.styled';
import { IoMdPerson, IoIosCall } from 'react-icons/io';
import Box from 'components/shared/Box';
import theme from 'theme';

const Contacts = ({ contacts }) => {
  //   console.log(contacts);
  return (
    <ContactsList>
      {contacts.map(contact => {
        const { id, firstName, lastName, tel } = contact;
        return (
          <Contact key={id}>
            <Box display="flex" alignItems="center">
              <IoMdPerson size={20} color={theme.colors.secondary} />
              <Name>
                {firstName}
                <span>&#x20;</span>
                {lastName}
              </Name>
            </Box>
            <Box display="flex" alignItems="center">
              <IoIosCall size={20} color={theme.colors.secondary} />
              <Phone>{tel}</Phone>
            </Box>
          </Contact>
        );
      })}
    </ContactsList>
  );
};

export default Contacts;
